@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format(woff2), url("../fonts/ptsans.woff") format(woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format(woff2), url("../fonts/ptsansbold.woff") format(woff);
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #f2f2f2;
}

.page__wrapper {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.page-header {
  position: relative;
  min-height: 56px;
}

.page-header__toggle {
  position: absolute;
  right: 20px;
  z-index: 10;
  height: 100%;
  margin: 0;
  padding: 17px 0 17px 0;
  background: rgba(0, 0, 0, 0);
  border: none;
  cursor: pointer;
}
.no-js .page-header__toggle {
  display: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__toggle:hover {
  opacity: 0.6;
}
.page-header__toggle:active {
  opacity: 0.3;
}

.page-header__toggle-line {
  display: block;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
}
.page-header__toggle-line--top {
  margin-bottom: 5px;
}
.page-header__toggle-line--low {
  margin-top: 5px;
}
.is-open .page-header__toggle-line {
  width: 27px;
  background-color: #ffffff;
}
.is-open .page-header__toggle-line--top {
  margin-bottom: 0;
  transform: rotate(405deg);
}
.is-open .page-header__toggle-line--mid {
  opacity: 0;
}
.is-open .page-header__toggle-line--low {
  margin-top: -8px;
  transform: rotate(-405deg);
}

.page-header__nav {
  position: relative;
}

.page-header__logo {
  position: absolute;
  left: 50%;
  z-index: 3;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
  cursor: default;
}
.page-header__logo--inner:hover {
  cursor: pointer;
  filter: drop-shadow(0 -2px 3px #333333);
}
.page-header__logo--inner:active {
  filter: drop-shadow(0 -2px 2px #333333);
}
.page-header__logo--inner:focus-visible {
  outline: none;
  filter: drop-shadow(0 -2px 3px #333333);
}

.page-header__logo-image {
  display: block;
}

.page-header__list {
  position: absolute;
  z-index: 5;
  display: none;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}
.no-js .page-header__list {
  position: relative;
  display: grid;
}
.is-open .page-header__list {
  display: grid;
}
@media (max-width: 767px) {
  .page-header__list {
    background-color: #81b3d2;
    gap: 1px;
  }
}
@media (min-width: 768px) {
  .page-header__list {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}

@media (max-width: 767px) {
  .page-header__item {
    box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .page-header__item:last-child {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .page-header__item {
    display: flex;
    justify-content: center;
  }
  .page-header__item:nth-child(4n-1) {
    grid-column-start: 4;
  }
}

.page-header__link {
  display: block;
  padding: 17px 55px 12px 55px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .page-header__link:hover {
    background-color: #669ec0;
  }
  .page-header__link:active {
    color: rgba(255, 255, 255, 0.3);
    background-color: #5496bd;
  }
}
@media (min-width: 768px) {
  .page-header__link {
    padding: 18px 20px 17px 20px;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
  .page-header__link--current {
    color: #766357;
    outline: none;
    cursor: default;
  }
  .page-header__link:not(.page-header__link--current):hover {
    color: #81b3d2;
  }
  .page-header__link:not(.page-header__link--current):active {
    color: rgba(0, 0, 0, 0.3);
  }
  .page-header__link:not(.page-header__link--current):focus-visible {
    color: #81b3d2;
    background-color: #f2f2f2;
    outline: none;
  }
}

.page-main {
  flex-grow: 1;
}

.heading {
  text-align: center;
  text-transform: uppercase;
}

.heading__title {
  max-width: 450px;
  margin: 0 auto;
  margin-bottom: 24px;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .heading__title {
    margin-bottom: 32px;
    font-size: 30px;
    line-height: 32px;
  }
}

.heading__description {
  max-width: 445px;
  margin: 0 auto;
  color: #333333;
}

.button {
  display: block;
  padding: 16px 30px 16px 30px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:focus-visible {
  outline: 3px #81b3d2 solid;
  outline-offset: 3px;
}
.button:disabled {
  color: #ffffff;
  background-color: #e5e5e5;
  cursor: default;
}

