.advantages__services-item {
  position: relative;
  padding: 8px 0 25px 47px;
}
.advantages__services-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
