}
.advantages__services-item--souvenirs::before {
  background-image: url("../img/svg/gift-icon.svg");
}
.advantages__services-item .advantages__subtitle {
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .advantages__services-item .advantages__subtitle {
    margin-bottom: 23px;
  }
}
@media (max-width: 767px) {
  .advantages__services-item {
    max-width: 400px;
