    background-size: 81px 79px;
  }
}
@media (min-width: 1200px) {
  .advantages__services-item {
    padding: 169px 50px 69px 50px;
  }
  .advantages__services-item--housing::before {
    top: 66px;
    width: 80px;
    height: 78px;
    background-size: 80px 78px;
  }
  .advantages__services-item--eating::before {
    top: 68px;
    width: 77px;
    height: 78px;
    background-size: 73px 78px;
  }
  .advantages__services-item--souvenirs::before {
