.photocards__likes {
  position: relative;
  display: block;
  padding: 4px 0 4px 37px;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
}
.photocards__likes::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 21px;
  background-color: #000000;
